<template lang="pug">
  .transaction.secondary(v-if="trx")
    .columns.is-multiline
      .column.is-narrow
        .line(
          v-if="trx.value" 
          :class="sender ? 'green' : 'red'"
        )
          span.amount {{ sender ? '+' : '-'}} {{ toUmi(amount) }} UMI
          span.amount.is-ultralight ≈ {{ pretty(amount) }}

          span.is-pulled-right.date.is-ultralight.is-hidden-tablet.is-hidden-desktop.is-hidden-widescreen.is-hidden-fullhd(v-if="trx.confirmed_at") {{ trx.confirmed_at | moment("DD.MM.YY HH:mm:ss [GMT] Z") }}
      
        .line.icons(v-if="trx.block_height")
          span.is-ultralight {{ t('Block') }}: {{ trx.block_height }}
          a.is-ultralight(v-clipboard:copy="trx.block_height" v-clipboard:success="onCopy")
            SvgIcon(name="content-copy")
          a.has-text-link(:href="$blockchain + 'block/' + trx.block_height" target="_blank")
            SvgIcon(name="open-in-new")
        
        .date.is-ultralight.is-hidden-mobile(v-if="trx.confirmed_at")
          | {{ trx.confirmed_at | moment("DD.MM.YY HH:mm:ss [GMT] Z") }}

      .column
        .has-text-right
          .line.icons
            SvgIcon.icon(:name="sender ? 'green-arrow' : 'red-arrow'")
            span.amount.is-ultralight(v-if="isSmartTrx") {{ toUmi(trx.value) }} UMI
            a.address(:href="$blockchain + 'address/' + (sender ? trx.sender : trx.recipient)") {{ cut(sender ? trx.sender : (trx.recipient || trx.sender)) }}
            a.is-ultralight(v-clipboard:copy="sender ? trx.sender : trx.recipient" v-clipboard:success="onCopy")
              SvgIcon.icon(name="content-copy")
            a.has-text-link(:href="$blockchain + 'address/' + (sender ? trx.sender : trx.recipient)" target="_blank")
              SvgIcon.icon(name="open-in-new")

          .line.icons(v-if="isSmartTrx")
            SvgIcon.icon(:name="sender ? 'green-arrow' : 'red-arrow'")
            span.amount.is-ultralight(v-if="trx.fee_value") {{ toUmi(trx.fee_value) }} UMI
            a.address(:href="$blockchain + 'address/' + trx.fee_address") {{ cut(trx.fee_address) }}
            a.is-ultralight(v-clipboard:copy="trx.fee_address" v-clipboard:success="onCopy")
              SvgIcon.icon(name="content-copy")
            a.has-text-link(:href="$blockchain + 'address/' + trx.fee_address" target="_blank")
              SvgIcon.icon(name="open-in-new")
              
          .icons(v-if="trx.hash")
            SvgIcon.icon(:name="trx.confirmations ? 'red-check' : 'green-check'")
            a.is-ultralight.hash(:href="$blockchain + 'transaction/' + trx.hash" target="_blank") tx: {{ cut(trx.hash, 15) }}
            a.is-ultralight(v-clipboard:copy="$blockchain + 'transaction/' + trx.hash" v-clipboard:success="onCopy")
              SvgIcon.icon(name="content-copy")
            a.has-text-link(:href="$blockchain + 'transaction/' + trx.hash" target="_blank")
              SvgIcon.icon(name="open-in-new")

</template>

<script>
import SvgIcon from "@/components/common/SvgIcon";

export default {
  name: "TrxShort",
  components: { SvgIcon },
  computed: {
    direction() {
      return this.address && this.trx.sender != this.address ? "+" : "-";
    },
    sender() {
      return this.address && this.trx.sender != this.address;
    },
    isSmartTrx() {
      return !this.sender && this.trx.fee_address && this.trx.version <= 1;
    },
    amount() {
      return this.isSmartTrx
        ? parseInt(this.trx.value || 0) + parseInt(this.trx.fee_value || 0)
        : parseInt(this.trx.value || 0);
    }
  },
  props: {
    trx: {
      default: null
    },
    address: {
      default: null
    }
  }
};
</script>

<style scoped lang="scss">
.transaction {
  margin-bottom: 0.25rem;
  padding: 1.5rem;

  .line {
    margin-bottom: 1.25rem;

    &:last-child {
      margin-bottom: 0;
    }
  }

  .icons > *:not(:first-child) {
    margin-left: 0.35rem;
  }
}
.amount {
  white-space: nowrap;
}
.amount:not(:first-child),
.address {
  padding-left: 0.35rem;
  text-decoration: none;
}
</style>
