<template lang="pug">
  .structure-stats(:class="{ 'resized': !!title }")
    h3(v-if="title") {{ title }}
    Widgets(:stats="stats")
</template>

<script>
import Widgets from "@/components/common/Widgets";

export default {
  name: "StructureStats",
  components: { Widgets },
  data() {
    return {
      balance: null
    };
  },
  props: {
    structure: {
      default: {
        staking: 0,
        referral: 0,
        stats: {
          coins: 0,
          members: 0,
          income: 0
        }
      }
    },
    title: {
      type: String,
      default: null
    }
  },
  computed: {
    staking() {
      let periods = 30;
      let effective_interest = parseInt(this.structure.deposit_percent);
      let nominal_interest =
        periods * ((1 + effective_interest / 10000) ** (1 / periods) - 1);
      return {
        day: (nominal_interest / 30) * 100,
        month: this.structure.deposit_percent / 100
      };
    },
    coefficient() {
      const effective_interest = this.structure.deposit_percent;
      const periods = 30 * 24 * 60 * 60 * 10;
      return (1 + effective_interest / 10000) ** (1 / periods);
    },
    stats() {
      return [
        {
          label: this.$translate.text("Staking rate"),
          numbers: [
            {
              number: this.staking.day.toFixed(3),
              suffix: "% " + this.$translate.text("per day")
            },
            {
              prefix: " / ",
              number: this.staking.month,
              suffix: "% " + this.$translate.text("per month")
            }
          ],
          customClass: "is-narrow is-narrow-mobile"
        },
        {
          label: this.$translate.text("Fee"),
          number: this.structure.fee_percent / 100,
          suffix: "%",
          customClass: "is-narrow is-narrow-mobile"
        },
        {
          label: this.$translate.text("Coins"),
          number: this.balance,
          suffix: "UMI",
          customClass: "is-narrow-mobile"
        },
        {
          label: this.$translate.text("Members"),
          number: this.structure.address_count || 0,
          customClass: "is-narrow-mobile"
        },
        {
          label: this.$translate.text("Structure Income"),
          number: this.structure.profit_percent / 100,
          suffix: "%",
          customClass: "is-narrow is-narrow-mobile"
        }
      ];
    }
  },
  methods: {
    growInit() {
      const start = new Date();
      this.timer = setInterval(() => {
        let time_delta = new Date() - start;
        this.balance =
          this.structure.balance * this.coefficient ** (time_delta / 100);
      }, 100);
    }
  },
  created() {
    this.growInit();
  }
};
</script>

<style scoped>
h3 {
  padding-bottom: 1rem;
}
.structure-stats {
  padding: 0 1rem;
}
</style>
