<template lang="pug">
  .structures
    .level
      .level-left
        h2.has-text-centered-mobile(v-translate) UMI structures
      //.level-right
        .level-item
          .tags.are-medium
            .tag(:class="{'is-active' : filter === null}" @click="toggleFilter()") Сортировать по рейтингу
            .tag(:class="{'is-active' : filter === 'all'}" @click="toggleFilter()") {{ t('All') }} {{ count }}
            .tag(:class="{'is-active' : filter === 'connected'}" @click="toggleFilter()") {{ t('Connected') }} {{ connected }}
    .structure.secondary(v-for="(item, i) in availiable" :key="i")
      .columns.is-multiline.is-flex-mobile.is-vcentered
        .column.is-narrow.is-narrow-mobile
          .icon.custom-icon
            img(v-if="item.prefix == 'roy'" v-webp="require('@/assets/structures/roy.png')")
            img(v-if="item.prefix == 'isp'" v-webp="require('@/assets/structures/isp.png')")
        .column.is-2
          .name {{ item.name }}
        .column
          .description(v-if="item.description") {{ t(item.description) }}
        //.column.is-narrow-mobile
          .balance.is-ultralight(:class="{'is-invisible': !item.balance}") {{ t('Balance') }} <span class="green">{{ toUmi(item.balance) }}</span>
        .column.is-narrow.is-6-mobile(v-if="item.url")
          a.has-text-link(:href="item.url" target="_blank") {{ t('Structure site') }}
            | 
            .icon.is-small
              SvgIcon.icon(name="open-in-new")
        //.column.has-text-right.is-6-mobile
          a.green(v-if="item.connected" @click="connect(item.prefix)") 
            CustomIcon.status(size="extra-small" url="connected.png")
            | {{ t('Connected') }}
          a.red(v-else @click="connect(item.id)")
            CustomIcon.status(size="extra-small" url="disconnected.png")
            | {{ t('Not connected') }}
        
      StructureStats(
        :structure="item"
        class="is-paddingless"
      )

</template>

<script>
import StructureStats from "@/components/StructureStats";
import SvgIcon from "@/components/common/SvgIcon";
import CustomIcon from "@/components/common/CustomIcon";

import { mapGetters } from "vuex";

export default {
  name: "Structures",
  components: { StructureStats, SvgIcon, CustomIcon },
  data() {
    return {
      filter: "all"
    };
  },
  computed: {
    ...mapGetters(["structures"]),
    availiable() {
      return this.structures.filter(item => item.prefix !== "umi");
    },
    count() {
      return this.availiable.length;
    },
    connected() {
      return this.availiable.filter(item => item.connected).length;
    }
  },
  methods: {
    toggleFilter(filter = null) {
      this.filter = filter;
    }
  }
};
</script>

<style scoped lang="scss">
.structures {
  padding: 0 0.75rem;

  .structure {
    margin-bottom: 1.5rem;
    padding: 1.5rem;

    img {
      width: 1.75rem;
      height: auto;
    }
    .name {
      font-weight: bold;
    }
    .balance {
      margin-left: 2.5rem;
    }
    .green {
      display: inline-block;
      padding-left: 0.5rem;
    }
    .status {
      display: inline-block;
      vertical-align: middle;
    }
  }
}
</style>
