<template lang="pug">
  .level
    .level-left
    .level-right
      b-pagination(
        :total="total"
        :current.sync="current"
        :range-before="rangeBefore"
        :range-after="rangeAfter"
        :order="order"
        :size="size"
        :simple="isSimple"
        :rounded="isRounded"
        :per-page="perPage"
        aria-next-label="Next page"
        aria-previous-label="Previous page"
        aria-page-label="Page"
        aria-current-label="Current page")
</template>

<script>
export default {
  data() {
    return {
      rangeBefore: 3,
      rangeAfter: 1,
      current: 1,
      order: "",
      size: "",
      isSimple: false,
      isRounded: false
    };
  },
  props: {
    total: { type: Number, default: 0 },
    perPage: { type: Number, default: 0 }
  },
  watch: {
    current: function(value) {
      this.$emit("change", {
        limit: this.perPage,
        offset: this.perPage * (value - 1)
      });
    }
  }
};
</script>
