<template lang="pug">
  .container
    b-tabs(
      v-model="activeTab",
      :animated="false",
      @input="changeTab"
      multiline
      destroy-on-hide
    )
      b-tab-item(
        label="UMI"
        icon="umi"
        ref="umi"
      )
        WalletInfo(prefix="umi") 

      b-tab-item(
        v-for="(item, i) in structures"
        :key="i"
        :ref="item.prefix"
        :visible="item.connected"
      )
        template(slot="header")
          .icon.custom-icon
            img(v-if="item.prefix === 'roy'" v-webp="require('@/assets/structures/roy.png')")
            img(v-if="item.prefix === 'isp'" v-webp="require('@/assets/structures/isp.png')")
          span {{ item.name }}
          
        StructureStats(
          :structure="item"
        )
        WalletInfo(
          :prefix="item.prefix"
          :structure="item"
        )

      b-tab-item(
        v-if="structures.length > 0"
      )
        template(slot="header")
          .icon.custom-icon
            SvgIcon(name="plus")
          span.custom-tab.dotted {{ t('Start staking') }}
        StartStaking(@goToStructures="goToStructures()")

      b-tab-item(
        v-if="structures.length > 0"
      )
        template(slot="header")
          .icon
            SvgIcon(name="apps")
          span.custom-tab.has-text-link {{ t('All structures') }}
        Structures

    template(v-if="activeTab <= structures.length")
      .level(v-if="stats.all > 0")
        .level-left
          h3.has-text-centered-mobile(v-translate) Transactions
        .level-right
          .level-item
            .tags.are-medium
              .tag(:class="{'is-active' : filter === 'all'}" @click="toggle('all')") {{ t('All') }} {{ stats.all }}
              .tag(:class="{'is-active' : filter === 'sended'}" @click="toggle('sended')" v-if="stats.sended") {{ t('Sent') }} {{ stats.sended }}
              .tag(:class="{'is-active' : filter === 'received'}" @click="toggle('received')") {{ t('Received') }} {{ stats.received }}

      .transactions(v-if="transactions && transactions.length > 0")
        TrxShort(
          v-if="transactions"
          v-for="(trx, i) in transactions"
          :key="i"
          :trx="trx"
          :address="wallet.address"
        )
      .is-size-5.custom-bg(v-else-if="wallet.isNew") {{ t("You haven’t had any transactions yet.") }}

      Pagination(
        v-if="stats[filter] >= params.limit"
        @change="pagination"
        :perPage="params.limit"
        :total="total"
      )

</template>

<script>
import WalletInfo from "@/views/tabs/WalletInfo";
import StartStaking from "@/views/tabs/StartStaking";
import Structures from "@/views/tabs/Structures";

import TrxShort from "@/components/TrxShort";
import StructureStats from "@/components/StructureStats";

import Pagination from "@/components/common/Pagination";
import SvgIcon from "@/components/common/SvgIcon";

import { mapGetters } from "vuex";

export default {
  name: "Wallet",
  components: {
    WalletInfo,
    StartStaking,
    Structures,

    TrxShort,
    StructureStats,

    Pagination,
    SvgIcon
  },
  props: {
    lastTab: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      activeTab: 0,
      trxVisible: true,
      interval: null
    };
  },
  computed: {
    ...mapGetters([
      "structures",
      "wallet",
      "loading",
      "transactions",
      "stats",
      "params",
      "filter"
    ]),
    total() {
      return this.stats[this.filter];
    },
    tabsCount() {
      return document.querySelectorAll(".tabs li").length - 1;
    }
  },
  methods: {
    changeTab(tab) {
      if (tab === 0) {
        this.$store.dispatch("changeAddress", "umi");
      } else if (tab <= this.structures.length) {
        this.$store.dispatch("changeAddress", this.structures[tab - 1].prefix);
      }
    },
    goToStructures() {
      this.activeTab = this.tabsCount;
    },
    hideTab() {
      this.structureCreated = true;
      this.activeTab = this.activeTab - 1;
    },
    toggle(type = "any") {
      this.$store.commit("setFilter", type);
      this.fetch();
    },
    pagination(params) {
      this.$store.commit("setParams", params);
      this.fetch();
    },
    fetch() {
      this.$store.dispatch("fetchTransactions", this.params);
    }
  },
  created() {
    this.interval = setInterval(() => {
      this.fetch();
    }, 3000);
  },
  beforeDestroy: function() {
    clearInterval(this.interval);
  }
};
</script>

<style scoped>
pre {
  color: #fff;
}
.transactions {
  margin-bottom: 1.5rem;
}
.custom-tab {
  font-weight: 300;
}
.dotted:before {
  content: "";
  position: absolute;
  left: 4px;
  right: 4px;
  bottom: 4px;
  top: 3px;
  border: 1px dashed #6b7381;
  border-radius: 2px;
}
.all {
  position: relative;
  bottom: -8px;
}
</style>

<style>
.is-active span.dotted:before {
  content: none;
}
</style>
