<template lang="pug">
  .columns.is-multiline.is-flex-mobile(v-if="prefix && wallet")
    .column.is-12-mobile.is-5-tablet.is-6-desktop
      .card.is-address-widget.without-background
        .card-content
          h4.level
            .level-left
              SvgIcon(name="barcode") 
              | {{ t('Your address') }}
          .media.with-margins
            .media-left
              img.qr(@click="toggleModal" :src="'https://chart.googleapis.com/chart?chs=256x256&cht=qr&chl=' + address + '&chf=bg,s,252A32'")
            .media-content.address {{ address }}
            .media-right
              a.button.is-primary.is-normal(v-clipboard:copy="address" v-clipboard:success="onCopy")
                SvgIcon.icon(name="content-copy-white")
          .balance.has-text-centered.linear-border
            h2.upper {{ toUmi(realPart * 100) }}
              span.percents(v-if="prefix !== 'umi' && fakePart") {{ fakePart }}
              SvgIcon.sign(name="sign")
            .is-ultralight ≈ {{ pretty(realPart * 100) }}
            a(v-if="prefix !== 'umi' && structure.url" :href="structure.url" target="_blank") {{ t('Structure site') }}
              .icon
                SvgIcon(name="open-in-new")

    .column.is-12-mobile.is-7-tablet.is-6-desktop
      .card.is-address-widget.without-background.fullheight
        form.card-content(v-if="sendMode" @submit.prevent="send")
          h4.level
            .level-left
              SvgIcon(name="send") 
              | {{ t('Send') }}
          .media.with-margins(:class="!isAddressValid ? 'is-danger' : ''")
            .media-left.is-ultralight
              label(for="recipient" v-translate) To the address
            .media-content
              textarea.textarea.has-fixed-size(
                v-model="recipient"
                v-focus
                @change="checkAddress"
                id="recipient"
                required
                :placeholder="t('Enter the wallet address which you want to send money to')"
                rows="2"
              )
          .columns.is-variable.is-1.is-vcentered.is-flex-mobile.is-multiline
            .column.is-7.is-12-mobile.is-7-tablet
              .media.with-field
                label.media-left.is-ultralight(for="amount" v-translate) Amount
                .media-content
                  input#amount.input(
                    v-model="amount"
                    type="number"
                    :step="0.01"
                    lang="en-150"
                    @keydown="getFiat"
                    @keyup="getFiat"
                    @change="getFiat"
                    required
                    :placeholder="0"
                  )
                .media-right.is-ultralight.upper UMI

            .column.is-1.has-text-centered.is-custom-mobile.is-1-tablet ≈
            .column.is-4
              .media.with-field
                .media-content
                  input.input(
                    v-model="amountInFiat"
                    type="number"
                    :step="decimal > 2 ? 0.00000001 : 0.01"
                    lang="en-150"
                    :placeholder="0"
                    @keydown="getUmi"
                    @keyup="getUmi"
                    @change="getUmi"
                  )
                .media-right.is-ultralight {{ currency }}

          .level
            .level-left
              span.is-ultralight(v-translate) Remaining balance:
              span.remains {{ toUmi(remains) }} UMI
            .level-right
              a(@click="allIn" v-translate) Send all

          .buttons
            button.button.gradient-button(
              type="submit"
              :disabled="!(this.isAddressValid && this.amount > 0 && this.amount <= this.inUmi)"
              v-translate
            ) Send
            a.button.is-link(@click="createSign" v-translate) Create signature
        
        .card-content(v-else-if="confirmMode")
          h3.green(v-translate) Transfer confirmation
          table.table.block-table
            tr
              td(v-translate) Amount
              td 
                h2 {{ toUmi(amount*100) }} UMI
            tr
              td(v-translate) To the address
              td.hash {{ recipient }}
            tr 
              td(v-translate) New balance
              td {{ toUmi(remains) }} UMI

          .buttons
            a.button.gradient-button(
              @click="confirm"
              :class="{'is-loading': loading}"
              v-translate
            ) Confirm
            a.button.is-link(@click="back" v-translate) Cancel

        .card-content(v-if="signMode")
          h3.green(v-translate) Generate signature
          .media.with-margins
            .media-content
              textarea.textarea.has-fixed-size(
                v-model="message"
                @change="sign"
                @keyPress="sign"
                :placeholder="t('Insert a message to create a signature')"
                rows="2"
              )
          .signature(v-if="signature") {{ signature }}

          .buttons(v-if="!signature")
            .button.gradient-button(
              @click="sign"
            ) {{ t('Generate') }}
            .button.is-link(@click="back" v-translate) Back
          .buttons(v-else)
            .gradient-button(
              v-clipboard:copy="signature" 
              v-clipboard:success="onCopy"
            ) {{ t('Copy signature') }}
            .button.is-link(@click="back" v-translate) Back

    .modal(:class="{'is-active':modalActive}")
      .modal-background(@click="toggleModal")
      .modal-content
         p.image.is-256x256
          img.qr(:src="'https://chart.googleapis.com/chart?chs=256x256&cht=qr&chl=' + address + '&chf=bg,s,FFFFFF'")
      button.modal-close.is-large(aria-label="close" @click="toggleModal")

</template>

<script>
import { mapActions, mapGetters } from "vuex";
import SvgIcon from "@/components/common/SvgIcon";
import * as umi from "@umi-top/umi-core-js";

export default {
  name: "WalletInfo",
  components: { SvgIcon },
  data() {
    return {
      recipient: null,
      isAddressValid: true,
      amount: null,
      amountInFiat: null,
      sendMode: true,
      confirmMode: false,
      signMode: false,
      message: null,
      signature: null,
      modalActive: false,
      realPart: "",
      fakePart: "",
      timer: null,
      updater: null
    };
  },
  props: {
    prefix: {
      type: String,
      default: "umi"
    },
    structure: {
      default: null
    }
  },
  computed: {
    ...mapGetters([
      "loading",
      "wallet",
      "signedMessage",
      "currency",
      "decimal",
      "course"
    ]),
    address() {
      return this.wallet.address;
    },
    balance() {
      return this.realPart
        ? Math.round(this.realPart * 100)
        : this.wallet.balance;
    },
    remains() {
      return this.amount > 0 && this.amount <= this.balance
        ? this.balance - Math.round(this.amount * 100)
        : this.balance;
    },
    inUmi() {
      return this.balance / 100;
    },
    coefficient() {
      const effective_interest = this.wallet.interest;
      const periods = 30 * 24 * 60 * 60 * 10;
      return (1 + effective_interest / 10000) ** (1 / periods);
    }
  },
  methods: {
    ...mapActions(["sendTransaction", "signMessage"]),
    growInit() {
      const start = new Date();
      let tmp_balance = this.wallet.balance
      this.timer = setInterval(() => {
        let time_delta = new Date() - start;
        let value =
          tmp_balance * this.coefficient ** (time_delta / 100);
        let balance = (value / 100).toFixed(5).split("");

        this.fakePart = "";
        this.realPart = "";
        for (var i = balance.length - 1; i >= 0; i--) {
          if (i > balance.length - 4)
            this.fakePart = balance[i] + this.fakePart;
          else this.realPart = balance[i] + this.realPart;
        }
      }, 100);
    },
    toggleModal() {
      this.modalActive = !this.modalActive;
    },
    getFiat() {
      if (this.amount > this.inUmi) this.amount = this.inUmi;
      if (this.amount < 0) this.amount = null;
      if (this.amount > 0 && this.amount < 1) this.amount = parseFloat(this.amount);

      if (this.amount)
        this.amountInFiat = (this.amount * this.course).toFixed(this.decimal);
      else this.amountInFiat = null;
    },
    getUmi() {
      if (this.amountInFiat < 0) this.amountInFiat = null;
      if (this.amountInFiat)
        this.amount = (this.amountInFiat / this.course).toFixed(2);
      else this.amount = null;
    },
    checkAddress() {
      try {
        umi.Address.fromBech32(this.recipient.trim());
        if (this.address === this.recipient.trim()) this.isAddressValid = false;
        else this.isAddressValid = true;
      } catch {
        this.isAddressValid = false;
      }
    },
    allIn() {
      this.amount = this.inUmi;
      this.getFiat();
    },
    send() {
      if (this.isAddressValid && this.amount > 0 && this.amount <= this.inUmi) {
        this.sendMode = false;
        this.confirmMode = true;
      }
    },
    async confirm() {
      if (!this.loading) {
        let result = await this.sendTransaction({
          recipient: this.recipient.trim(),
          value: this.amount
        });
        if (result && result.hash) {
          this.recipient = null;
          this.amount = null;
          this.amountInFiat = null;
          this.$buefy.snackbar.open({
            message:
              "Транзакция " + this.cut(result.hash, 10) + " отправлена в сеть!",
            actionText: null,
            duration: 10000
          });
        } else {
          this.$buefy.snackbar.open({
            message: "Не получилось совершить перевод.",
            type: "is-danger"
          });
        }
        this.back();
      }
    },
    createSign() {
      this.sendMode = false;
      this.signMode = !this.signMode;
    },
    sign() {
      this.signMessage(this.message.trim());
      this.signature = this.signedMessage;
    },
    back() {
      this.sendMode = true;
      this.confirmMode = false;
      this.signMode = false;
      this.signature = false;
    },
    update() {
      clearInterval(this.timer);
      if (this.wallet.interest && !this.wallet.special) {
        this.growInit();
      } else {
        this.realPart = this.wallet.balance / 100;
      }
    }
  },
  created() {
    this.update();
    setInterval(() => this.update(), 30000)
  },
  beforeDestroy() {
    clearInterval(this.timer);
  },
  watch: {
    wallet: function() {
      this.update();
    },
    currency: function() {
      this.getFiat();
    }
  }
};
</script>

